import React, { Component } from 'react';
import './style.css';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar';
import { EmailIcon, MusicIcon, GithubIcon, LocationIcon } from './Icons';

const styles = {
    avatar: {
        height: '30vh',
        width: '30vh',
        margin: '0 auto',
    },
    button: {
        textTransform: 'none'
    },
    card: {
        textAlign: 'center',
        maxWidth: 345,
        margin: '0 auto',
        marginTop: '5vw'
    },
    media: {
        height: 0,
        paddingTop: '56.25%'
    },
    typography: {
        color: 'gray'
    }
};

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showMail: false
        }
    }

    handleOnclick = () => {
        this.setState({showMail: !this.state.showMail})
    }

    render() {
        const { classes } = this.props;
        return (
            <div className="App">
                <Card className={classes.card}>
                    <Avatar src={'https://avatars3.githubusercontent.com/u/21347724?s=460&v=4'} className={classes.avatar}/>
                    <CardContent>
                        <Typography gutterBottom variant="headline" component="h2">
                        Jonas Erixon
                        </Typography>
                        <Typography gutterBottom variant="subheading" component="h2" className={classes.typography}>
                        <LocationIcon /> Kalmar, Sweden
                        </Typography>

                        <br />

                        <Typography component="p">
                            Web Developer, Music Producer
                        </Typography>
                    </CardContent>
                    <CardActions>
                        <a href='https://github.com/jonaserixon'>
                            <Button size="small" color="primary">
                                <GithubIcon />
                                Github
                            </Button>
                        </a>

                        <a href='https://soundcloud.com/a-himitsu'>
                            <Button size="small" color="primary">
                                <MusicIcon />
                                Music
                            </Button>
                        </a>

                        <Button size="small" color="primary" className={classes.button} onClick={this.handleOnclick}>
                            <EmailIcon />
                            {this.state.showMail ? 'x.jonaz@gmail.com' : 'CONTACT'}
                        </Button>
                    </CardActions>
                </Card>
            </div>
        );
    };
}

App.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(App);



